<template>
<transition appear name="navigationShow" v-show="this.$store.state.pageShow">
  <div id="Navigation">
      <img src="../assets/images/ShouYe.svg" :class="[nowRouter=='Homepage'?'':'imgDisappear']" @click="to('Homepage')">
      <img src="../assets/images/ZhanNeiXiaoXi.svg" :class="[nowRouter=='News'?'':'imgDisappear']" @click="to('News')">
      <img src="../assets/images/GeRenZhongXin.svg" :class="[nowRouter=='PersonalCenter'?'':'imgDisappear']" @click="to('PersonalCenter')">
  </div>
</transition>
</template>

<script>
export default {
    name:"NavigationCP",
    mounted(){
        this.nowRouter=window.location.href.split('/')[window.location.href.split('/').length-1];
        console.log(this.nowRouter)
    },
    data(){
        return{
            nowRouter:""
        }
    },
    methods:{
        to(router){
            this.$store.state.pageShow=0;
            setTimeout(()=>this.$router.push({path:router}),300);
            setTimeout(()=>this.$store.state.pageShow=1,500);
        }
    }
}
</script>

<style>
#Navigation{
    display:flex;
    justify-content:space-around;
    width:100%;
    position:fixed;
    top:88%;
    height:12%;
    align-items:center;
    border-radius: 2vh;
    flex-direction:row;
    background:#F3F3F3;
    box-shadow: 0vh -.5vh .5vh rgba(0, 0, 0, 0.2);
}
img{
    width:5vh;
    height:5vh;
}
.imgDisappear{
    opacity: .5;
}
.navigationShow-enter-active{
    animation: navigationShow .3s;
}
.navigationShow-leave-active{
    animation: navigationShow .3s reverse;
}
@keyframes navigationShow{
    0%{top:100%;}
    100%{top:88%}
}
</style>